import React, { useState, useEffect } from 'react';
import { config } from '../config';
import FadeIn from './FadeIn';

const PlacementOpportunities = () => {
  const [placements, setPlacements] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    fetchPlacements();
  }, []);

  const fetchPlacements = async () => {
    try {
      setLoading(true);
      const response = await fetch(config.GOOGLE_APPS_SCRIPT_URL);
      const data = await response.json();
      
      if (data.success) {
        setPlacements(data.placements || []);
        setError(null);
      } else {
        setError(data.error || 'Failed to fetch placements');
      }
    } catch (err) {
      console.error('Error fetching placements:', err);
      setError('Failed to load placement opportunities');
    } finally {
      setLoading(false);
    }
  };

  const handleApplyNow = (link) => {
    if (link && link !== '#') {
      window.open(link, '_blank');
    }
  };

  if (loading) {
    return (
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {Array.from({ length: 6 }).map((_, i) => (
          <div key={i} className="animate-pulse">
            <div className="bg-gray-200 h-48 rounded-t-2xl"></div>
            <div className="bg-white p-6 rounded-b-2xl">
              <div className="h-6 bg-gray-200 rounded mb-3"></div>
              <div className="h-4 bg-gray-200 rounded mb-2"></div>
              <div className="h-4 bg-gray-200 rounded mb-4"></div>
              <div className="h-10 bg-gray-200 rounded"></div>
            </div>
          </div>
        ))}
      </div>
    );
  }

  if (error) {
    return (
      <div className="text-center py-12">
        <div className="text-red-500 text-lg mb-4">⚠️</div>
        <p className="text-textSecondary">{error}</p>
        <button 
          onClick={fetchPlacements}
          className="mt-4 px-6 py-2 bg-primary text-white rounded-lg hover:bg-primary/90 transition-colors"
        >
          Try Again
        </button>
      </div>
    );
  }

  if (placements.length === 0) {
    return (
      <div className="text-center py-12">
        <div className="text-gray-400 text-4xl mb-4">📋</div>
        <p className="text-textSecondary text-lg">No placement opportunities available at the moment.</p>
        <p className="text-textSecondary">Check back later for new opportunities!</p>
      </div>
    );
  }

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
      {placements.map((placement, index) => (
        <FadeIn key={placement.id} delay={index * 0.1} direction="up">
          <div className={`bg-white rounded-2xl shadow-lg overflow-hidden transition-all duration-300 hover:shadow-xl ${
            placement.status === 'Closed' ? 'opacity-60' : ''
          }`}>
            <div className="h-48 bg-gradient-to-br from-primary/10 to-secondary/10 flex items-center justify-center">
              <div className="text-6xl text-primary">💼</div>
            </div>
            
            <div className="p-6">
              <div className="mb-4">
                <h3 className="text-xl font-bold text-textPrimary mb-2">{placement.title}</h3>
                <p className="text-primary font-medium mb-2">{placement.subheading}</p>
                <p className="text-textSecondary text-sm leading-relaxed">{placement.description}</p>
              </div>
              
              <div className="flex items-center justify-between">
                {placement.status === 'Yes' ? (
                  <button
                    onClick={() => handleApplyNow(placement.link)}
                    className="bg-gradient-to-r from-primary to-secondary text-white px-6 py-3 rounded-xl font-semibold hover:shadow-lg transition-all duration-200"
                  >
                    Apply Now
                  </button>
                ) : placement.status === 'Closed' ? (
                  <div className="bg-gray-100 text-gray-600 px-6 py-3 rounded-xl font-semibold">
                    Closed
                  </div>
                ) : null}
                
                <div className={`text-xs px-3 py-1 rounded-full ${
                  placement.status === 'Yes' ? 'bg-green-100 text-green-700' :
                  placement.status === 'Closed' ? 'bg-red-100 text-red-700' :
                  'bg-gray-100 text-gray-700'
                }`}>
                  {placement.status}
                </div>
              </div>
            </div>
          </div>
        </FadeIn>
      ))}
    </div>
  );
};

export default PlacementOpportunities;
